import {
  MarkProperties,
  Block,
} from '@ali/4ever-cangjie';
import { spacing as spacingTheme } from '../theme';
import { SpacingUtil } from '../utils/formats/spacing';
import { SzUtil } from '../utils/formats/sz';
import { unitsConverter } from "../utils/toUnits";


const oldDefaultMark: MarkProperties = {
  type: 'sz',
  data: {
    value: 14,
  },
};

const oldMark: MarkProperties = {
  type: 'sz',
  data: {
    value: 24,
  },
};

const newMark: MarkProperties = {
  type: 'sz',
  data: {
    value: 24,
    szUnit: 'pt',
  },
};

const minMark: MarkProperties = {
  type: 'sz',
  data: {
    value: 2,
  },
};

const maxMark: MarkProperties = {
  type: 'sz',
  data: {
    value: 2400,
  },
};

describe('Test getLineHeightFromSpacing', () => {
  it('Test basic lineHeight', () => {
    expect(SpacingUtil.getLineHeightFromSpacing({
      line: 1,
    })).toBe('1.7');
    expect(SpacingUtil.getLineHeightFromSpacing({
      line: 1.15,
    })).toBe('2');
    expect(SpacingUtil.getLineHeightFromSpacing({
      line: 1.5,
    })).toBe('2.5');
    expect(SpacingUtil.getLineHeightFromSpacing({
      line: 2,
    })).toBe('3.4');
    expect(SpacingUtil.getLineHeightFromSpacing({
      line: 2.5,
    })).toBe('4.3');
    expect(SpacingUtil.getLineHeightFromSpacing({
      line: 3,
    })).toBe('5.1');
    expect(SpacingUtil.getLineHeightFromSpacing({
      line: '1.46666px',
    })).toBe('1.02');
    expect(SpacingUtil.getLineHeightFromSpacing({
      line: '18px',
    })).toBe('1.9124999999999999');
  });

  it('Test other lineHeight', () => {
    expect(SpacingUtil.getLineHeightFromSpacing({
      line: 1.2,
    })).toBe('2.04');
    expect(SpacingUtil.getLineHeightFromSpacing({
      line: 0.06,
    })).toBe('1.02');
    expect(SpacingUtil.getLineHeightFromSpacing({
      line: 2.1,
    })).toBe('3.57');
  });

  it('Test exact lineHeight', () => {
    expect(SpacingUtil.getLineHeightFromSpacing({
      line: 1,
      lineRule: 'exact',
    })).toBe('16px');
    expect(SpacingUtil.getLineHeightFromSpacing({
      line: 2,
      lineRule: 'exact',
    })).toBe('32px');
  });
});

describe('Test SzUtil.fromMark', () => {
  it('Test mark', () => {
    expect(SzUtil.fromMark(undefined as unknown as MarkProperties)).toBe(11);
    expect(SzUtil.fromMark(oldDefaultMark)).toBe(11);
    expect(SzUtil.fromMark(oldMark)).toBe(18);
    expect(SzUtil.fromMark(newMark)).toBe(24);
    // min max
    expect(SzUtil.fromMark(minMark)).toBe(5);
    expect(SzUtil.fromMark(maxMark)).toBe(200);
  });
});

describe('Test SzUtil.toPt', () => {
  it('Test toPt', () => {
    expect(SzUtil.toPt('14px')).toBe(10.5);
    expect(SzUtil.toPt('24px')).toBe(18);
    expect(SzUtil.toPt('14pt')).toBe(14);
    expect(SzUtil.toPt('2em')).toBe(22);

    expect(SzUtil.toPx('14px')).toBe(14);
    expect(SzUtil.toPx('24px')).toBe(24);
    expect(SzUtil.toPx('14pt')).toBe(18.5);
    expect(SzUtil.toPx('2em')).toBe(29.5);
  });

  it('Test get invalid sz', () => {
    expect(SzUtil.toPt(undefined)).toBe(11);
    expect(SzUtil.toPt(null)).toBe(11);
    expect(SzUtil.toPt('')).toBe(11);

    expect(SzUtil.toPx(undefined)).toBe(11);
    expect(SzUtil.toPx(null)).toBe(11);
    expect(SzUtil.toPx('')).toBe(11);
  });
});

describe('Test getSpacingLineFromHtml', () => {
  it('Test absolute units', () => {
    expect(SpacingUtil.fromHtml('27px')).toBe(1.15);
    expect(SpacingUtil.fromHtml('27px', '14pt')).toBe(1);

    expect(SpacingUtil.fromHtml('27pt')).toBe(1.5);
    expect(SpacingUtil.fromHtml('27pt', '14pt')).toBe(1.15);
  });

  it('Test relative units', () => {
    expect(SpacingUtil.fromHtml('200%')).toBe(2);
    expect(SpacingUtil.fromHtml('2')).toBe(1.15);
    expect(SpacingUtil.fromHtml('2em')).toBe(1.15);

    expect(SpacingUtil.fromHtml('200%', '14pt')).toBe(2);
    expect(SpacingUtil.fromHtml('2', '14pt')).toBe(1);
    expect(SpacingUtil.fromHtml('2em', '14pt')).toBe(1);
  });

  it('Test invalid units', () => {
    expect(SpacingUtil.fromHtml('20vw')).toBe(1);
    expect(SpacingUtil.fromHtml('xxx')).toBe(1);
    expect(SpacingUtil.fromHtml('')).toBe(1);
  });
});

describe('Test SpacingUtil.fromBlock', () => {
  it('before/after', () => {
    const paragraph = {
      data: {
        spacing: {
          before: 32,
          after: 20,
        },
      },
    };
    const spacing = SpacingUtil.fromBlock(paragraph as unknown as Block);

    expect(spacing.before).toBe(32);
    expect(spacing.after).toBe(20);
  });
  it('before/after pt', () => {
    const paragraph = {
      data: {
        spacing: {
          before: '20pt',
          after: '10pt',
        },
      },
    };
    const spacing = SpacingUtil.fromBlock(paragraph as unknown as Block);

    expect(spacing.before).toBe(unitsConverter.toPX('20pt'));
    expect(spacing.after).toBe(unitsConverter.toPX('10pt'));
  });
  it('before/afterLines', () => {
    const paragraph = {
      data: {
        spacing: {
          beforeLines: 1,
          afterLines: 2,
        },
      },
    };
    const spacing = SpacingUtil.fromBlock(paragraph as unknown as Block);

    expect(spacing.before).toBe(spacingTheme.defaultLinePitch);
    expect(spacing.after).toBe(spacingTheme.defaultLinePitch * 2);
  });
  it('before/afterAutospacing', () => {
    const paragraph = {
      data: {
        spacing: {
          beforeAutospacing: true,
          afterAutospacing: true,
        },
      },
    };
    const spacing = SpacingUtil.fromBlock(paragraph as unknown as Block);

    expect(spacing.before).toBe(spacingTheme.defaultBeforeAutospacing);
    expect(spacing.after).toBe(spacingTheme.defaultAfterAutospacing);
  });
});
